@import '../../Styles/variables.scss';

.projection-form {
  .projection-timeline {
    margin-top: 30px;
    overflow-x: auto;

    .leaf-input-box {
      width: fit-content;

      .ant-input-number-input {
        text-align: center;
      }
    }

    .root-input-box {
      width: fit-content;

      .ant-input-number-input {
        text-align: center;
        // background-color: #f0e8ff;
        background-color: #fbf6f7;
      }
    }

    .l1-input-box {
      width: fit-content;

      .ant-input-number-input {
        text-align: center;
        background-color: #ffdbdb;
      }
    }

    .l2-input-box {
      width: fit-content;

      .ant-input-number-input {
        text-align: center;
        background-color: #ffdbdb;
      }
    }

    &::-webkit-scrollbar {
      height: 8px;
    }

    &::-webkit-scrollbar-track {
      background: #f0e9fd;
    }

    &::-webkit-scrollbar-thumb {
      background: #d5d0dd;
    }

    &::-webkit-scrollbar-thumb:hover {
      cursor: pointer;
    }

    &.combined {
      .ant-input-number-input {
        color: $text-color;
      }
    }
  }

  .action-row {
    margin-top: 40px;
  }
}
